@import url(https://fonts.googleapis.com/css?family=Lato:100,300,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
*{
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header{width:100%;border-bottom:1px solid #eee;background-color:#fff;display:flex;align-items:center;padding:15px 20px}.App-header .logo{display:inline-block;width:150px}.App-header .logo img{width:100%}.App-header .header{display:inline-block;margin-left:50px}.App-header .header .active{color:#ff6a00}.App-header .header a{display:inline-block;text-decoration:none;padding:15px;min-width:80px;color:#383838;text-align:center}.App-header .header a:hover{color:#ff6a00}.App-container{padding:20px}iframe{width:100%;height:calc(100vh - 85px)}

.Home *{line-height:1.5}.Home p{text-indent:15px}


.centered-container{background-color:#fff;display:inline-flex;padding:4rem;border-radius:.125rem;border:1px solid rgba(0,0,0,0.1);box-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.04)}.link{color:#2175FF;cursor:pointer;font-weight:400;text-decoration:none}.link--arrowed{display:inline-block;height:2rem;line-height:2rem}.link--arrowed .arrow-icon{position:relative;top:-1px;transition:transform 0.3s ease;vertical-align:middle}.link--arrowed .arrow-icon--circle{transition:stroke-dashoffset .3s ease;stroke-dasharray:95;stroke-dashoffset:95}.link--arrowed:hover .arrow-icon{transform:translate3d(5px, 0, 0)}.link--arrowed:hover .arrow-icon--circle{stroke-dashoffset:0}#page1{background:rgba(0,0,0,0.5)}.wrapper{display:table;height:100%;width:100%}.wrapper .stage{display:table-cell;vertical-align:middle}.wrapper .tabled,.wrapper .middled{text-align:center;margin:0 auto}.wrapper .thin{font-weight:300}.wrapper .thick{font-weight:900}.wrapper a{text-transform:uppercase;font-size:36px;color:white;text-decoration:none;position:relative;display:block}.wrapper p{font-weight:100;font-size:80%;margin:1px}.wrapper [class^="link-"]{display:inline-block;margin:2em}.wrapper .link-1 a:before,.wrapper .link-1 a:after{content:'';border-bottom:solid 1px white;position:absolute;bottom:0;width:0}.wrapper .link-1 a:before{left:0}.wrapper .link-1 a:after{right:0}.wrapper .link-1 a:hover:before,.wrapper .link-1 a:hover:after{width:50%}.wrapper .link-1 a:before,.wrapper .link-1 a:after{transition:all 0.2s ease}.wrapper .link-2 a:hover{color:#5CAAEF}.wrapper .link-2 a:before{content:'';border-bottom:solid 1px #5CAAEF;position:absolute;bottom:0;left:0;width:100%;opacity:0}.wrapper .link-2 a:hover:before{opacity:1.0}.wrapper .link-2 a,.wrapper .link-2 a:before{transition:all 0.2s ease}.wrapper .link-3 a:before{content:'';border-bottom:solid 1px white;position:absolute;bottom:0;left:0;width:100%}.wrapper .link-3 a:hover:before{transform:scaleY(4)}.wrapper .link-3 a:before{transition:all 0.3s ease}.wrapper .link-4 a:before{content:'';border-bottom:solid 1px white;position:absolute;bottom:0;left:0;width:100%}.wrapper .link-4 a:hover:before{border-bottom:solid thin #5CAAEF}.wrapper .link-4 a:before{transition:all 0.2s ease}.wrapper .link-5 a:before{content:'';border-bottom:solid 1px white;position:absolute;bottom:0;left:0;width:100%}.wrapper .link-5 a:hover:before{transform:scaleY(4);border-bottom:solid thin #5CAAEF}.wrapper .link-5 a:before{transition:all 0.3s ease}.wrapper .link-6 a:hover{transform:scale(1.05)}.wrapper .link-6 a{transition:all 0.1s linear}.wrapper .link-7 a:before{content:'';border-bottom:solid 1px #d9534f;position:absolute;bottom:0;left:30%;width:0;transform:scale(0)}.wrapper .link-7 a:hover:before{border-bottom:solid thin #d9534f;width:40%;-webkit-animation:heartbeat-x 1.7s infinite ease-in;animation:heartbeat-x 1.7s infinite ease-in}.wrapper .link-7 a:hover{-webkit-animation:heartbeat 1.7s infinite ease-in;animation:heartbeat 1.7s infinite ease-in}@-webkit-keyframes heartbeat{0%{-webkit-transform:scale(1)}10%{-webkit-transform:scale(1.1)}20%{-webkit-transform:scale(1)}30%{-webkit-transform:scale(1.1)}40%{-webkit-transform:scale(1)}}@-webkit-keyframes heartbeat-x{0%{-webkit-transform:scaleX(0)}10%{-webkit-transform:scaleX(1)}20%{-webkit-transform:scaleX(0)}30%{-webkit-transform:scaleX(1)}40%{-webkit-transform:scaleX(0)}}@keyframes heartbeat{0%{transform:scale(1)}10%{transform:scale(1.1)}20%{transform:scale(1)}30%{transform:scale(1.1)}40%{transform:scale(1)}}@keyframes heartbeat-x{0%{transform:scaleX(0)}10%{transform:scaleX(1)}20%{transform:scaleX(0)}30%{transform:scaleX(1)}40%{transform:scaleX(0)}}

